import { request } from "@/utils/request";
import { UploadUrl, REQUEST_SUCCEEDED_CODE } from "@/common/constants";
import aes from '@/utils/aes/export';
/**
 * realNameAuthentication 实名认证界面
 * @description 实名认证界面
 * @Author: 穆兰
 * @Date: 2022/1/9
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/9
 */
export default {
    data() {
        return {
            isSuccessed: false,
            fileUrl: '' //提交照片的url
        };
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            console.log("onNarLeftClick()");
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description  点击选择图片的方框
         */
        onImageContentClick() {
            let _this = this;
            uni.chooseImage({
                count: 1,
                sizeType: ['original', 'compressed'],
                sourceType: ['camera'],
                success: function (res) {
                    // 返回选定照片的本地文件路径列表，tempFilePath可以作为img标签的src属性显示图片
                    var tempFiles = res.tempFilePaths;
                    _this.fileUrl = tempFiles[0];
                }
            });
        },
        /**
         * @description 点击隐私政策文字
         */
        onPrivacyPolicyClick() {
            uni.navigateTo({
                url: "/pages/login/bugu-privacy-policy/bugu-privacy-policy"
            });
        },
        /**
         * @description 点击提交按钮
         */
        onSubmitButtonClick() {
            if (!this.fileUrl) {
                uni.showToast({
                    title: "请先选择照片",
                    icon: "error"
                });
                return;
            }
            this.submit_authentication(this.fileUrl);
        },
        /**
         * @description  提交实名认证
         * @param {string}  fileUrl 文件地址
         */
        async submit_authentication(fileUrl) {
            uni.showLoading({
                title: '正在提交'
            });
            //先获取上传图片的凭证
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'user',
                    action: 'verify/token',
                    data: {},
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                },
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                let fileName = res.data.data.fileName;
                let fileToken = res.data.data.token;
                let _this = this;
                uni.uploadFile({
                    url: UploadUrl,
                    filePath: fileUrl,
                    name: 'file',
                    formData: {
                        'key': fileName,
                        'token': aes.decrypt(fileToken)
                    },
                    async success(fileRes) {
                        console.log(fileRes);
                        let authenticationChangeRes = await request({
                            data: {
                                method: 'PUT',
                                group: 'user',
                                action: 'verify/submit',
                                data: {
                                    verifyPic: fileName,
                                },
                                header: {
                                    'content-type': 'application/x-www-form-urlencoded',
                                },
                            }
                        });
                        uni.hideLoading();
                        if (authenticationChangeRes.data.code === REQUEST_SUCCEEDED_CODE) {
                            _this.isSuccessed = true;
                        }
                    }
                });
            }
        }
    }
};
